import React, {Component} from "react";
import { Alert } from '@mui/material';

class BasicAlert extends Component {
  constructor(props) {
    super(props);
    this.severity = null;
  }

  render() {
    return(
        <Alert className='Alert' severity={this.severity}>{this.props.text}</Alert>
    )
  }
};

class InfoAlert extends BasicAlert {
  constructor(props) {
    super(props);
    this.severity = 'info';
  }
};

class WarnAlert extends BasicAlert {
  constructor(props) {
    super(props);
    this.severity = 'warning';
  }
};

class ErrorAlert extends BasicAlert {
  constructor(props) {
    super(props);
    this.severity = 'error';
  }
}

export {InfoAlert, WarnAlert, ErrorAlert};